<template>
  <v-container>
    <v-row align="center" justify="center">
      <v-col cols="12" lg="8">
        <v-card>
          <v-toolbar>
            <v-btn @click="$router.go(-1)" icon>
              <v-icon>mdi-arrow-left</v-icon>
            </v-btn>
            <v-toolbar-title>
              {{ $t('Game {0}', [id]) }}
            </v-toolbar-title>
            <v-spacer />
            <slot name="menu" />
          </v-toolbar>
          <v-card-text>
            <key-value-table
              name="game"
              :api="`/api/history/games/${id}/details`"
              :headers="headers"
            >
              <template v-slot:gameable.path="{ item: { gameable: { path } } }">
                {{ path.map(i => i ? $t('Right') : $t('Left')).join(', ') }}
              </template>
              <template v-slot:gameable.bucket="{ item: { gameable: { bucket } } }">
                {{ bucket + 1 }}
              </template>
            </key-value-table>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import KeyValueTable from '~/components/KeyValueTable'

export default {
  components: { KeyValueTable },

  props: ['id'],

  computed: {
    headers () {
      return [
        { text: this.$t('Path'), value: 'gameable.path' },
        { text: this.$t('Bucket'), value: 'gameable.bucket' },
        { text: this.$t('Created at'), value: 'gameable.created_at' },
        { text: this.$t('Updated at'), value: 'gameable.updated_at' }
      ]
    }
  }
}
</script>
